<template>
<div class="CouponSearch">
    <topNav title="核销历史" url="/CouponUse"></topNav>
    <div class="dateAll">
        <div class="dataBox">
            <div class="data">
                <datepik @change='dateChange' style="margin:0 auto;float:none;" limit="pre" step="1"></datepik>
            </div>
            <div class="line"></div>
            <div class="allBox">
                共 <b>{{masterTotal}}</b> 笔
            </div>
        </div>
        <div class="searchBox">
            <div class="search">
                <gpicker ref="pccode" @change="savepccode"></gpicker>
                <input class="orderNumer" type="text" name="" id="" placeholder="会员卡号" v-model='searchOption.cardNo'>
                <div class="searchResult" @click='search'>查询</div>
            </div>
        </div>
    </div>
    <div class="scrollBox" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div class="orderList" v-for='item in listInfo' :key='item.index'>
            <div class="blueLind"></div>
            <div class="top">
                券号&nbsp;：{{item.no}}
            </div>
            <div class="infoL">
                <div class="lineInfo">
                    <div class="titleThird nomalL">名称</div>&nbsp;:
                    <span>{{item.couponName}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">营业点</div>&nbsp;:
                    <span>{{item.pccode}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">价值</div>&nbsp;:
                    <span>{{item.realValue}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">核销时间</div>&nbsp;:
                    <span>{{item.consumeDate | fd}}</span>
                </div>
            </div>
            <div class="infoL infoR">
                <div class="lineInfo">
                    <div class="titleThird nomalL">会员卡号</div>&nbsp;:
                    <span>{{item.cardNo | niming}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">姓名</div>&nbsp;:
                    <span>{{item.cardName}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">核销人</div>&nbsp;:
                    <span>{{item.consumeUser}}</span>
                </div>
                <div class="lineInfo">
                    <div class="titleThird nomalL">状态</div>&nbsp;:
                    <span>{{item.sta}}</span>
                </div>
            </div>

        </div>
        <div v-bind:class="{ 'hide': !isEmpty }">
            <img class="nothing" src="../../assets/img/nothing.png">
            <p class="nothingTxt">什么都没有</p>
        </div>
        <div class="infoWarn" v-if='cutOff==1'> —— 我是有底线的 —— </div>
    </div>
</div>
</template>

<script>
import datepik from "../common/datepik";
import moment from "moment";
import gpicker from "../common/gpicker";
import {
    InfiniteScroll,
    Actionsheet,
    Toast
} from "mint-ui";

export default {
    data() {
        return {
            couponPs:!!localStorage.ps
        ? JSON.parse(localStorage.ps)
        : {
            pccode: null,
            pcname: null,
          },
            cutOff: "",
            isEmpty: true,
            masterTotal: 0,
            listInfo: [],
            searchOption: {
                beginDate: moment().format("YYYY-MM-DD"),
                endDate: moment().add(1, 'day').format("YYYY-MM-DD"),
                cardNo: "",
                pccode: "",
                userSource:"YPOS",
                couponType: null,
                maxResults: 10,
                firstResult: 0
            },
        };
    },
    computed: {},
    mounted() {
        this.searchOption.pccode = this.couponPs.pccode;
        this.getparams();
        this.getSum();
        this.getInfo();
    },
    filters: {
        tofixed(v) {
            return parseFloat(v).toFixed(2);
        },
         niming(v){
            if(v && v.length > 4){
                return v.substr(0,2)+"***"+v.substr(v.length-2);
            }
            return v;
        },
       fd(value) {
        if (value == null || value == "") return "";
        return value.substr(11);
      }
    },
    methods: {
        search() {
            //查询的时候充值分页
            this.searchOption.firstResult = 0;
            this.listInfo = [];
            //this.getInfo();
            this.getSum();
        },
         getSum() {
            this.$http.post("/pos/posUnion/findCouponSum", this.searchOption).then(res => {
                if (res.data.errorCode == 0) {
                    var count = 0;
                    if (res.data.result) {
                        for (var i = 0; i < res.data.result.length; i++) {
                            count += parseInt(res.data.result[i].count);
                        };
                    }
                    this.masterTotal = count;
                    if(this.masterTotal > 0){
                         this.getInfo();
                    }
                    else{
                        this.isEmpty = true;
                    }
                } else {
                    this.msg(res.data.errorMsg);
                }
            })
        },
        getparams() {
            this.$http.post("/pos/posUnion/listpccode",{}).then(res => {
                if (res.data.errorCode == 0) {
                      var psv = [];
                    _.forEach(res.data.result, (el, key) => {
                        psv.push({
                            code: el.code,
                            name: el.descript
                        });
                    });
                    this.$refs.pccode.setData(psv, this.couponPs.pccode);
                } else {
                    this.msg(res.data.errorMsg);
                }
            })
        },
         savepccode() {
            this.searchOption.pccode = this.$refs.pccode.getValue();
            this.search();
        },
        dateChange(value) {
            //console.log("dateChange")
            this.searchOption.beginDate = moment(value).format("YYYY-MM-DD");
            this.searchOption.endDate = moment(value).add(1, 'day').format("YYYY-MM-DD");
            this.listInfo = [];
            this.search();
        },

        getInfo(isloadMore) {
            this.cutOff = 0;
            this.$http
                .post("/pos/posUnion/findCouponDetailDtoList", this.searchOption)
                .then(res => {
                    if (res.data.errorCode == 0) {
                       // this.masterTotal = res.data.retVal.totalCount;
                        this.isEmpty = false;
                        if (isloadMore) {
                            // 加载更多
                            _.forEach(res.data.result, (el, key) => {
                                this.listInfo.push(el);
                            });
                        } else {
                            // 第一次加载数据
                            this.listInfo = res.data.result;
                        }
                        //判断是否已经到底了的

                        if (this.masterTotal >= 4 && this.masterTotal <= this.searchOption.firstResult + this.searchOption.maxResults) {
                            this.cutOff = 1;
                        }

                        this.loading = false;
                        if (this.masterTotal == "0") {
                            //this.msg('查询结果为0笔')
                            this.isEmpty = true;
                            this.cutOff = 0;
                        }
                    } else {
                        this.msg(res.data.errorMsg);
                    }
                });
        },
        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        },
        loadMore() {
            // 自动加载内容
            //console.log("loadMore")
            if (
                this.masterTotal >=
                parseInt(this.searchOption.maxResults) +
                parseInt(this.searchOption.firstResult)
            ) {
                this.searchOption.firstResult += this.searchOption.maxResults;
                this.loading = true;
                let sef = this;
                var isloadMore = true;
                sef.getInfo(isloadMore);
            }
        }
    },
    components: {
        actionsheet: Actionsheet,
        datepik,
        gpicker
    }
};
</script>

<style lang="less">
.CouponSearch {
    text-align: center;
    overflow-y: hidden;

    .dateAll {
        width: 100%;
        height: 2.88rem;
        background: #fff;
        position: fixed;
        margin-top: -1px;
        z-index: 2000;

        .dataBox {
            margin: 0 auto;
            width: 96%;
            height: 100%;

            .data {
                width: 59%;
                height: 1.0667rem;
                margin-top: 0.1067rem;
                display: inline-block;

                .iconBox {
                    border-radius: 2.6667rem;
                    width: 0.8533rem;
                    height: 0.8533rem;
                    display: inline-block;
                    text-align: center;
                    background: #fff;
                    -moz-transform: none;
                    -webkit-transform: none;
                    -o-transform: none;
                    -ms-transform: none;
                    transform: none;

                    .icon-jiantou,
                    .icon-caret-right-copy {
                        font-size: 0.32rem;
                        line-height: 0.88rem;
                        color: #d8d8d8;
                        transform: scale(0.7);
                    }
                }

                .blakBg {
                    background: #f6f8f9 !important;
                    border-radius: 2.6667rem;
                }

                .fontBlack {
                    color: black !important;
                }

                .dataDtail {
                    display: inline-block;
                    font-size: 0.3733rem;
                    color: #393c41;
                    margin: 0.2667rem 0.3733rem 0rem;
                }
            }

            .line {
                width: 0.0267rem;
                text-align: center;
                display: inline-block;
                vertical-align: top;
                margin-top: 0.3733rem;
                height: 0.5867rem;
                background: #cccccc;
            }

            .allBox {
                width: 38%;
                margin-top: 0.48rem;
                height: 1.0667rem;
                display: inline-block;
                color: #393c41;
                font-size: 0.37rem;
                vertical-align: top;
                b{
                  color:red;
                }
            }
        }

        .searchBox {
            height: 1.6rem;
            margin: 0 auto;
            width: 100%;
            position: fixed;
            background: #f4f5f7;
            margin-top: -1px;
            top: 2.3733rem;

            .search {
                width: 94%;
                margin: 0 auto;
                height: 0.8rem;
                padding-top: 0.4rem;

                .gpicker {
                    width: 3.3rem;
                    float: left;
                    position: relative;
                    border: 1px solid #ccc;
                    background: #fff;
                    border-radius: 0.1333rem;
                }

                .tableNuber {
                    width: 21%;
                    height: 0.7733rem;
                    font-size: 0.3733rem;
                    padding-left: 0.2667rem;
                    background: #fff;
                    border: 0.0267rem solid #cccccc;
                    border-radius: 0.1333rem;
                    margin-left: 0.1333rem;
                    float: left;
                }

                .orderNumer {
                    width: 3rem;
                    height: 0.7733rem;
                    font-size: 0.3733rem;
                    padding-left: 0.2667rem;
                    background: #fff;
                    border: 0.0267rem solid #cccccc;
                    border-radius: 0.1333rem;
                    float: left;
                    margin-left: 0.2667rem;
                }

                .searchResult {
                    width: 20.5%;
                    height: 0.7733rem;
                    font-size: 0.3733rem;
                    background: #d8af68;
                    border-radius: 0.1867rem;
                    float: right;
                    color: #fff;
                    line-height: 0.7467rem;
                    margin-right: 0.08rem;
                    margin-top: 0.0533rem;
                }
            }
        }
    }

    .scrollBox {
        position: fixed;
        margin-top: 2.9rem;
        width: 100%;
        height: 79%;
        overflow-y: auto;
        padding-bottom: 2rem;

        .orderList {
            background: #ffffff;
            margin-bottom: 0.2667rem;
            width: 96%;
            height: auto;
            padding-bottom: 0.2667rem;
            margin: 0 auto 0.2667rem;
            box-shadow: 0 0 0.2133rem 0rem #dadada;
            border-radius: 0.1333rem;
            position: relative;

            .mintui {
                position: absolute;
                top: 30%;
                right: 5%;
                transform: rotate(180deg);
            }

            .top {
                width: 100%;
                height: 0.5rem;
                line-height: 0.5rem;
                padding-top: 0.3rem;
                text-align: left;
                margin-bottom: -0.4rem;
                margin-left: 0.2rem;
            }

            .blueLind {
                height: 0.3733rem;
                width: 1.1%;
                background: #646f83;
                display: inline-block;
                float: left;
                margin-top: 0.48rem;
            }

            .infoL {
                width: 49%;
                height: auto;
                text-align: left;
                display: inline-block;
                padding-top: 0.3733rem;

                .lineInfo {
                    margin: 0.1067rem auto;
                    color: #999999;

                    .titleThird {
                        display: inline-block;
                        color: #999999;
                        letter-spacing: 0.2667rem;
                    }

                    .titileS {
                        display: inline-block;
                        color: #999999;
                    }

                    .nomalL {
                        letter-spacing: 0rem;
                    }

                    span {
                        margin-left: 0.1333rem;
                        font-size: 0.3733rem;
                        color: #151515;
                    }
                }
            }

            .infoR {
                width: 45%;
            }

            .infoBo {
                text-align: left;
                margin-left: 0.3733rem;

                .titleThird {
                    display: inline-block;
                    color: #999999;
                    letter-spacing: 0.2667rem;
                }

                .titileS {
                    display: inline-block;
                    color: #999999;
                }

                .nomalL {
                    letter-spacing: 0rem;
                }

                span {
                    margin-left: 0.1333rem;
                    font-size: 0.3733rem;
                    color: #151515;
                }

                b {
                    font-size: 0.32rem;
                    color: #4990e2;
                    text-decoration: underline;
                    margin-left: 0.1333rem;
                }
            }
        }
    }

    .nothing {
        width: 80px;
        height: 88px;
        margin: 150px auto 0 auto;
    }

    .nothingTxt {
        text-align: center;
        font-size: 20px;
        color: #ccd1d5;
        margin-top: 20px;
    }

    .show {
        display: block;
    }

    .hide {
        display: none;
    }

    .mint-popup-right {
        width: 100%;
        height: 100%;
    }

    .infoWarn {
        height: 50px;
        color: #ccc;
    }
}
</style>



